import Request from '@/axios/request'

export function getThirdParties (params = {}) {
  return Request.get('thirdParty/index', {
    params
  })
}

export function getThirdParty (id) {
  return Request.get('thirdParty/show', { params: { id: id } })
}

export function createThirdParty (formData) {
  return Request.promisePost('thirdParty/create', formData)
}

export function editThirdParty (id, formData) {
  return Request.promisePost('thirdParty/edit', formData, {
    params: {
      id: id
    }
  })
}

export function lockThirdParty (id) {
  return Request.get('thirdParty/lock', { params: { id: id } })
}

export function unlockThirdParty (id) {
  return Request.get('thirdParty/unlock', { params: { id: id } })
}

export function lockThirdParties (ids) {
  return Request.post('thirdParty/locks', { ids: ids })
}

export function unlockThirdParties (ids) {
  return Request.post('thirdParty/unlocks', { ids: ids })
}

export function getLiveProviderItems () {
  return Request.get('thirdParty/liveProviderItems')
}

export function getThirdPartyItems (params = {}) {
  return Request.get('thirdParty/items', { params })
}
