<template>
  <a-modal
    :title="title"
    :width="640"
    :visible="visible"
    :confirmLoading="loading"
    @ok="
      () => {
        $emit('ok')
      }
    "
    @cancel="
      () => {
        $emit('cancel')
      }
    "
  >
    <a-spin :spinning="loading">
      <a-form-model ref="bindForm" :model="model" :rules="rules" v-bind="formItemLayout">
        <a-form-model-item label="合作方" prop="parter_id" help="注意：ipc的合作方会在绑定nvr后同步成nvr的合作方">
          <a-select show-search placeholder="请选择合作方" v-model="model.parter_id">
            <a-select-option v-for="(tp, key) in thirdPartyItems" :value="tp.id" :key="key">{{
              tp.partner_name
            }}</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-spin>
  </a-modal>
</template>

<script>
import { getThirdPartyItems } from '@/api/third-party'

const defaultModel = {
    parter_id: undefined,
    target_type: '',
    ids: []
}
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    data () {
        return {
            title: '绑定合作方',
            model: defaultModel,
            thirdPartyItems: {},
            formItemLayout: {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 }
            },
            rules: {
                parter_id: [{ required: true, message: '合作方必须选择', trigger: 'blur' }]
            }
        }
    },
    methods: {
        async loadThirdPartyItems () {
          this.thirdPartyItems = await getThirdPartyItems()
          console.log('====================================')
          console.log('thirdPartyItems:', this.thirdPartyItems)
          console.log('====================================')
        },
        resetForm () {
            this.model = defaultModel
            // this.$nextTick(() => {
            //    this.$refs.bindForm.resetFields()
            // })
        }
    },
    watch: {
        async visible (newValue, oldValue) {
            if (newValue) {
              this.loadThirdPartyItems()
            }
        }
    }
}
</script>
<style lang="less" scoped>
.ant-select {
    min-width: 220px;
}
</style>
